.author {
  z-index: 1;

  padding-bottom: 0.5rem;

  color: rgb(94, 101, 117);

  font-weight: bold;
}

.container {
  display: flex;
  overflow: hidden;
  align-content: stretch;
  align-items: stretch;
  flex-direction: column;
  justify-content: center;

  height: 100vh;
  padding-top: 1rem;

  border: none;
  background-color: rgb(222, 232, 240);
  box-shadow: 0 0.1rem 0.08rem 0.08rem rgba(44, 61, 94, 0.2),
    0 0.2rem 0.1rem 0.15rem rgba(70, 87, 121, 0.1);
}

.main {
  max-width: 25rem;
  margin: 0 auto;
}
