.container {
  align-self: flex-end;
  flex: 0 1 auto;

  width: 75%;
  margin-bottom: 0.5rem;
  padding: 0.35rem 0.65rem 0.45rem 0.8rem;

  text-align: left;

  color: white;
  border: none;
  border-radius: 1rem;
  background-color: rgb(25, 132, 255);
  box-shadow: 0 0.07rem 0.08rem 0.05rem rgba(31, 62, 121, 0.3);

  font-size: 16px;
}

.container_answer {
  align-self: flex-start;
  flex: 0 1 auto;

  width: 75%;
  margin-bottom: 0.5rem;
  padding: 0.4rem 0.65rem 0.55rem 0.8rem;

  text-align: left;

  color: rgb(31, 31, 31);
  border: none;
  border-radius: 1rem;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 0.08rem 0.05rem 0.05rem rgba(44, 61, 94, 0.1);

  font-size: 16px;
}

.time {
  padding-bottom: 0.1rem;

  color: rgba(0, 0, 0, 0.5);

  font-size: 13px;
}
