.container {
  position: relative;

  display: flex;
  overflow: hidden;
  align-content: stretch;
  align-items: stretch;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: flex-end;

  padding: 1.5rem 1rem;
}

.separation {
  position: absolute;
  z-index: 10;
  top: 0;

  width: 100%;
  height: 1.5rem;

  background-image: linear-gradient(
    rgba(222, 232, 240, 1),
    rgba(222, 232, 240, 0)
  );
}
