.button {
  padding: 0 0.6rem;

  cursor: pointer;
  transition: 0.08s;

  color: white;
  border: none;
  border-radius: 1.2rem;
  outline: none;
  background-color: rgb(25, 144, 255);

  font-size: 14px;
  font-weight: bold;
}

.button:disabled,
.button:disabled:hover,
.button:disabled:active {
  transform: none;

  background-color: rgb(178, 187, 202);
}

.button:hover {
  transform: translateY(-0.12rem);

  background-color: rgb(25, 151, 255);
}

.button:active {
  transform: translateY(-0.04rem);

  background-color: rgb(25, 132, 255);
}

.container {
  display: flex;

  padding: 1rem;

  background-color: rgb(7, 24, 39, 0.1);
}

.field {
  flex: 1 1 auto;

  margin-right: 0.5rem;
  padding: 0.35rem 0.65rem 0.45rem 0.8rem;

  color: rgb(31, 31, 31);
  border: none;
  border-radius: 1rem;
  outline: none;
  background-color: white;
  box-shadow: inset 0 0.1rem 0.02rem 0.02rem rgba(91, 106, 138, 0.35);

  font-size: 16px;
}

.field:required,
.field:invalid {
  box-shadow: none;
}

.field:disabled {
  background-color: rgb(178, 187, 202);
}
